import * as React from 'react'
import {useState,useEffect} from 'react';
import {useNavigate} from 'react-router-dom'
import {Box,Button,Card,CircularProgress,Grid,TextField,Typography} from '@mui/material';
import BackspaceIcon from '@mui/icons-material/Backspace';
import DataTable , { createTheme } from 'react-data-table-component';
import Constantes from './../Constantes';

//Input component  for filter search
const FilterComponent = ({ filterText, onFilter, onClear }) => (
  <>
    <TextField
      variant='filled'
      type='text'
      placeholder='Filter by title'
      value={filterText}
      onChange={onFilter}
      inputProps={{style: {
        height: '32px',
        width: '200px',
        borderRadius: '3px',
        borderTopLeftRadius: '5px',
        borderBottomLeftRadius: '5px',
        borderTopRightRadius: 0,
        borderBottomRightRadius: 0,
        border: '1px solid #e5e5e5',
        padding: '0 32px 0 16px',
        color: 'white'
      }}}
    />
    <Button 
      variant='contained' 
      onClick={onClear}
      style={{
        height: '34px',
        width: 'auto',
        borderRadius: '3px',
        borderTopLeftRadius: 0,
        borderBottomLeftRadius: 0,
        borderTopRightRadius: '5px',
        borderBottomRightRadius: '5px',
        textAlign: 'center',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
      }}
    ><BackspaceIcon/></Button>
  </>
);

//Custom Theme 
createTheme('solarized', {
  text: {
    primary: '#f7f7f7',
    secondary: '#f5f0ed',
  },
  background: {
    default: '#2d3436',
  },
  context: {
    background: '#cb4b16',
    text: '#FFFFFF',
  },
  divider: {
    default: '#3f4445',
  },
  action: {
    button: 'rgba(0,0,0,.54)',
    hover: 'rgba(0,0,0,.08)',
    disabled: 'rgba(0,0,0,.12)',
  },
}, 'dark');

export default function TaskList() {
  const navigate = useNavigate();

  const [data, setData] = useState([]);
  const [updated , setUpdated] = useState(false);
  const [pending, setPending] = useState(true);
  const [progress, setProgress] = useState(10);

  const [filterText, setFilterText] = useState('');
  const [resetPaginationToggle, setResetPaginationToggle] = useState(false);
  var filteredItems = data.filter(
    task => task.title && task.title.toLowerCase().includes(filterText.toLowerCase())
  );

  useEffect(() => {
    const timer = setInterval(() => {
      setProgress((prevProgress) => (prevProgress >= 100 ? setPending(false) : prevProgress + 10));
    }, 200);
    return () => {
      clearInterval(timer);
    };
  }, []);

  useEffect(() => {
    (async () => {
      const result = await fetch(`${Constantes.API_ROUTE}/tasks`, {
        method: 'GET',
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json'
        }
      });
      let data = await result.json();
      setData(data);
      setUpdated(false)
    })();
  }, [updated])

  const columns = [
    {
      name:'#',
      cell: (row, index) => index + 1,
      maxWidth: '6px'
    },
    {
      name:'Title',
      selector: row => row.title,
      sortable: true,
    },
    {
      name:'Description',
      selector: row => row.description,
      sortable: true,
    },
    {
    	button: true,
    	cell: (row) => <Button variant='contained' color='primary' onClick={() => navigate(`/task/${row.id}/edit`)} >Edit</Button>,
    }
    ,
    {
    	button: true,
    	cell: (row) => <Button variant='contained' color='error' onClick={() => handleDelete(row.id)} >Delete</Button>,
    }
  ]

  const paginationComponentOptions = {
  	rowsPerPageText: 'Filas por pgina',
    rangeSeparatorText: 'de',
    selectAllRowsItem: true,
    selectAllRowsItemText: 'Todos',
  };

  const CustomLoader = (props) => (
    <Grid container alignItems='center' justifyContent='center'>
      <Card sx={{mt: 5}} style={{backgroundColor: '#1e272e', padding:'1rem'}}>
        <Box sx={{ position: 'relative', display: 'inline-flex' }}>
          <CircularProgress variant="determinate" {...props} />
          <Box
            sx={{
              top: 0,
              left: 0,
              bottom: 0,
              right: 0,
              position: 'absolute',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
            }}
          >
            <Typography variant="caption" component="div" color='white'>
              {`${Math.round(props.value)}%`}
            </Typography>
          </Box>
        </Box>
      </Card>
    </Grid>
  );

  const customStyles = {
  	headCells: {
      style: {
        color: '#FFF',
        fontSize: '18px',
      },
    },
    rows: {
      highlightOnHoverStyle: {
        backgroundColor: 'rgb(83, 96, 99)',
        borderBottomColor: '#FFFFFF',
        borderRadius: '25px',
        outline: '1px solid #FFFFFF',
      },
    },
  };

  const subHeaderComponentMemo = React.useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText('');
      }
    };

    return (
      <FilterComponent 
        onFilter={e => setFilterText(e.target.value)} onClear={handleClear} filterText={filterText}
      />
    );
  }, [filterText, resetPaginationToggle]);

  const handleDelete = async (id) => {
    const result = await fetch(`${Constantes.API_ROUTE}/tasks/${id}`, {
      method: 'DELETE',
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json'
      }
    });
    let message = await result.json();
    if(message.message === 'Number of rows affected: 1'){
      filteredItems = data.filter(task => task.id !== id);
      setData(filteredItems);
    }
  }

  return (
    pending ? <CustomLoader value={progress}/> : 
    <Card sx={{mt: 1}} style={{backgroundColor: '#2d3436', padding:'1rem'}}>
      <DataTable 
        columns={columns} data={filteredItems} theme="solarized" customStyles={customStyles} 
        pagination 
        paginationComponentOptions={paginationComponentOptions} 
        paginationResetDefaultPage={resetPaginationToggle}
        subHeader
        subHeaderComponent={subHeaderComponentMemo}
        highlightOnHover
        persistTableHead 
        pointerOnHover
      />
    </Card>
  )
}
