import {Button, Card, CardContent, CircularProgress, Grid, TextField, Typography} from '@mui/material';
import {useState, useEffect} from 'react';
import {useNavigate , useParams} from 'react-router-dom';
import Constantes from "./../Constantes";

 export default function Form() {
  const [task, setTask] = useState({
    id: '' , title: '' , description:'' 
  });
  const [loading, setLoading] = useState(false);
  const [editing, setEditing] = useState(false)

  const navigate = useNavigate();
  const params = useParams();

  useEffect(() => {
    if(params.id){
      (async () => {
        const result = await fetch(`${Constantes.API_ROUTE}/tasks/${params.id}`);
        let data = await result.json();
        setTask(data[0]);
        setEditing(true);
      })();
    }
  } , [params.id])

  const submit = async (e) => {
    e.preventDefault();
    setLoading(true);
    await fetch(`${Constantes.API_ROUTE}/tasks/${(editing) ? params.id : ''}`, {
      method: ((editing) ? 'PUT' : 'POST'),
      mode: 'cors',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(task)
    })
    setLoading(false);
    navigate('/')
  }

  const handleChange = (e) => setTask({...task, [e.target.name] : e.target.value});

  return (
    <Grid container direction='column' alignItems='center' justifyContent='center'>
      <Grid item xs={3}>
        <Card sx={{mt: 5}} style={{backgroundColor: '#1e272e', padding:'1rem'}}>
          <Typography variant='5' textAlign='center' color='white'>
            {(editing) ? 'Edit Task' : 'Create Task'}
          </Typography>
          <CardContent>
            <form onSubmit={submit}>
              <TextField variant='filled' label='Write your tittle' 
                name='title'
                value={task.title}
                onChange={handleChange}
                inputProps={{style: {color: 'white'}}} 
                InputLabelProps={{style: {color: 'white'}}}
                sx={{display: 'block' , margin: '.5rem 0'}}/>
              <TextField variant='filled' label='Write your description' multiline rows={4} 
                name='description'
                value={task.description}
                onChange={handleChange}
                inputProps={{style: {color: 'white'}}} 
                InputLabelProps={{style: {color: 'white'}}}
                sx={{display: 'block' , margin: '.5rem 0'}}/>
              <Button variant='contained' color='success' type='submit' disabled={!task.title || !task.description }>
                {loading ? <CircularProgress color='inherit' size={24}/> : 'Save'}
              </Button>
            </form>
          </CardContent>
        </Card>
      </Grid>
    </Grid>
  )
}
